/*accordion*/

button.accordion,
button.subaccordion {
    background-color: #eee;
    color: #444;
    cursor: pointer;
    padding: 1px;
    width: 100%;
    border: none;
    text-align: left;
    outline: none;
    font-size: 15px;
    transition: 0.4s;
}

button.accordion.active,
button.accordion:hover,
button.subaccordion.active,
button.subaccordion:hover {
    background-color: #ccc;
}

button.accordion:after,
button.subaccordion:after {
    content: '\002B';
    color: #777;
    font-weight: bold;
    float: right;
    margin-left: 5px;
}

button.accordion.active:after,
button.subaccordion.active:after {
    content: "\2212";
}

