/**************************************Loading effect*/

.wrapperresponding {
    position: relative;
    height: 200px;
    width: 200px;
    top: 50%;
    margin-top: -100px;
    left: 50%;
    margin-left: -100px;
}

.wrapperresponding .responseerror {
    color: #f00;
    font-family: 'Helvetica', 'Arial', sans-serif;
    font-size: 5em;
    font-weight: bold;
    text-align: center;
    position: relative;
    height: 200px;
    width: 200px;
    top: 50%;
    margin-top: -100px;
    left: 50%;
    margin-left: -100px;
}

.wrapperresponding .text {
    color: red;
    text-align: center;
    font-size: 2em;
}

 .wrapperloading {
    position: relative;
    height: 200px;
    width: 200px;
    top: 50%;
    margin-top: -100px;
    left: 50%;
    margin-left: -100px;
}

.wrapperloading .loading.down {
    position: relative;
    height: 100px;
    width: 100px;
    top: 50%;
    margin-top: -50px;
    left: 50%;
    margin-left: -50px;
    border-radius: 150px;
    border: 3px solid lightsteelblue;
    border-left-color: lightsteelblue;
    border-top-color: rgb(75, 75, 124);
    border-bottom-color: #0080FF;
    border-right-color: lightsteelblue;
    animation: rotation 1s linear infinite;
}

@keyframes rotation {
    0% {
        transform: rotate(0deg);
    }
    50% {
        transform: rotate(180deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

