.resize-content {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}
.youtube-player {
  width: 100%;
  height: 100%;
}

.article-metaline {
  margin: 0;
  padding: 0;
  background-color: #008;
}
.article-metaline-right {
  position: absolute;
  right: 0;
  top: 0;
  background-color: #008;
}
.article-meta-tag {
  padding: 0 1ex;
  background-color: #999;
  color: #008;
}
.article-meta-value {
  padding: 0 1ex;
  background-color: #008;
  color: #999;
}

img {
  max-width: 100%;
  max-height: 800px;
}

/* article list */
#action-bar-container {
  position: fixed;
  /* width: 100%;*/
  background-color: #000;
  padding: 40px 0 0 0;
  top: 0;
  left: 0;
  overflow-y: hidden;
  overflow-x: auto;
  z-index: 98;
}
.action-bar {
  padding: 0;
  margin: 0 auto;
  height: 40px;
  box-sizing: border-box;
  text-align: center;
}
.action-bar .btn {
  height: 100%;
  line-height: 40px;
}
.action-bar > * {
  display: inline-block;
  box-sizing: border-box;
  height: 100%;
}
.btn-group {
  padding: 0;
  border: 0;
  font-size: 0;
}
.btn-group .btn {
  margin: 0;
}
.btn-group .btn + .btn {
  margin: 0 0 0 -1px;
}
.btn {
  position: relative;
  display: inline-block;
  border: 1px solid #666;
  background-color: #444;
  padding: 0 1ex;
  text-decoration: none;
  color: #ddd;
  box-sizing: border-box;
  font-size: small;
  text-align: center;
}
.btn.wide {
}
.btn:not(.disabled):hover {
  border-color: #eee;
  background-color: #bbb;
  color: #000;
  z-index: 2;
}
.btn.disabled {
  border-color: #111;
  background-color: #222;
  color: #666;
  z-index: 0;
  cursor: default;
}
.pull-right {
  float: right;
}
.btn:link,
.btn:visited {
  color: #ddd;
}
.btn.selected {
  border-color: #ccc;
  background-color: #bbb;
  color: #000;
  z-index: 2;
}
.btn.selected:hover {
  border-color: #eee;
}

/* article list table */
.r-list-container {
  padding-top: 10px;
}
.r-ent {
  position: relative;
  width: 100%;
  line-height: 120%;
  vertical-align: middle;
}
.r-ent > * {
  display: inline-block;
  vertical-align: middle;
  min-height: 1em;
}
.r-ent .nrec {
  width: 4ex;
  text-align: right;
  float: left;
  font-family: serif;
}
.r-ent .mark {
  width: 2ex;
  text-align: center;
  font-weight: bold;
  font-family: Inconsolata, sans-serif;
}
.r-ent .nrec,
.r-ent .title {
  padding: 0.5ex 0 0 0;
}
.r-ent .title,
.r-ent .meta {
  display: block;
}
.r-list .title a,
.r-list .title a:link {
  text-decoration: none;
  color: #ddd;
}
.r-list .title a:hover,
.r-list .title a:hover:visited {
  background-color: #aaa;
  color: #000;
}
.r-list .title a:visited {
  color: #999;
}
.r-ent .meta {
}
.r-ent .meta > * {
  display: inline-block;
  padding: 0.5ex 0;
}
.r-ent .meta .mark,
.r-ent .meta .date {
  float: right;
  text-align: right;
  margin: 0 0 0 5px;
}
.r-ent .date {
  font-family: serif;
  min-width: 6ex;
}
.r-ent .author {
  font-family: Inconsolata, serif;
}
.r-list-sep {
  height: 0.5ex;
  width: 100%;
  background-color: #555;
}

.article-menu {
  position: relative;
  float: right;
  width: 4ex;
  margin-left: 1ex;
}
.article-menu .trigger {
  text-align: center;
  height: 100%;
  margin: 0 0 0.5ex 0;
  cursor: pointer;
}
.article-menu.shown {
  background-color: #333;
}
.article-menu .dropdown {
  position: absolute;
  right: 0;
  background-color: #444;
  z-index: 90;
  display: none;
  white-space: nowrap;
}
.article-menu.shown .dropdown {
  display: block;
}
.article-menu .item a {
  display: block;
  padding: 1ex;
  text-decoration: none;
}
.article-menu .item a:hover {
  color: #000;
}

/* Desktop */
@media screen and (min-width: 800px) {
  .bbs-screen,
  #topbar,
  #navigation,
  .action-bar,
  #article-polling {
    /*min-width: 600px;*/
    max-width: 1200px;
  }

  .bbs-content,
  #article-polling {
    font-size: 18px; /* 24px*/
  }

  .b-ent .board {
    font-size: 14px; /* 20px*/
  }
  .b-ent .board > * {
    box-sizing: border-box;
  }
  .b-ent .board > :first-child {
    padding: 0 0 0 1ex;
  }
  .board-name {
    width: 20ex;
  }
  .board-nuser {
    width: 9ex;
    text-align: right;
    padding: 0;
    padding-right: 1ex;
  }
  .board-class {
    width: 6ex;
  }

  .search-bar {
    font-size: 14px; /* 20px*/
  }

  .r-ent {
    margin: 1ex 0;
    padding: 0.5ex 0;
    background-color: #111;
  }
  .r-ent > * {
    font-size: 14px; /* 20px*/
  }
  .r-ent .title {
    margin: 0 5ex;
  }
  .r-ent .meta {
    margin: 0 0 0 5ex;
  }

  .m-ent {
    padding: 0.5em;
  }
  .m-ent > * {
    font-size: 14px; /* 20px*/
  }

  .action-bar {
    text-align: left;
  }
  .btn-group-paging {
    /*float: right;*/
  }
  .btn {
    padding: 0 2ex;
    font-size: medium;
    font-size: initial;
  }
  .btn.wide {
    padding: 0 3ex;
  }

  div.over18-notice {
    margin: 20px;
  }
  .over18-button-container {
    display: inline-block;
  }
}

/* Mobile */
@media screen and (max-width: 799px) {
  .b-ent {
    border-bottom: 1px #aaa solid;
  }
  .b-ent .board {
    line-height: 120%;
  }
  .b-ent .board > * {
    box-sizing: border-box;
    clear: both;
  }
  .board-name {
    width: 100%;
    padding: 0 1ex;
  }
  .board-nuser {
    position: absolute;
    float: right;
    top: 0;
    right: 0;
    padding: 0.5ex 1ex;
  }
  .board-class {
    min-width: 7ex;
    padding: 0 0 0 1ex;
    font-size: small;
  }
  .board-title {
    padding: 0 1ex 0 0;
    font-size: small;
  }

  .r-ent {
    border-bottom: 1px solid #444;
  }
  .r-ent .title,
  .r-ent .meta {
    margin-left: 5ex;
    margin-right: 2ex;
  }
  .r-ent .date,
  .r-ent .mark {
  }

  .m-ent:first-child {
    border-top: 1px #aaa solid;
  }
  .m-ent {
    padding: 0;
    border-bottom: 1px #aaa solid;
  }
  .m-ent .title a {
    display: block;
    box-sizing: border-box;
    padding: 0 1ex;
    line-height: 200%;
    text-decoration: none;
  }

  .action-bar {
    /* If the screen is too small, we want buttons to overflow. */
    white-space: nowrap;
  }
  .btn-group-dir {
    width: 33%;
  }
  .btn-group-paging {
    width: 66%;
  }
  .btn-group-dir > .btn {
    width: 50%;
  }
  .btn-group-paging > .btn {
    width: 25%;
  }
  .btn-group-cls {
    width: 100%;
  }
  .btn-group-cls > .btn {
    width: 50%;
  }

  div.over18-notice {
    padding: 0 1ex;
  }
  .over18-button-container {
    box-sizing: border-box;
    width: 100%;
    padding: 1ex;
  }
  .over18-button-container button {
    width: 80%;
  }
}

