    padding: 5px;
}

svg {
    background: white;
    border: 0px;
}

div.tooltip {
    pointer-events: all;
}

div.tooltip.a {
    position: absolute;
    text-align: left;
    padding: 2px;
    font: 12px sans-serif;
    background: lightsteelblue;
    border: 1px;
    border-radius: 8px;
    pointer-events: none;
}

div.tooltip.b {
    position: absolute;
    text-align: left;
    padding: 2px;
    font: 12px sans-serif;
    background: lightsteelblue;
    border: 1px;
    border-radius: 8px;
    pointer-events: none;
}

div.tooltip.c {
    position: absolute;
}

