/* Style the tab */

div.tab {
    overflow: hidden;
    border: 1px solid #ccc;
    background-color: #f1f1f1;
}

/* Style the buttons inside the tab */

div.tab button {
    background-color: inherit;
    float: left;
    border: none;
    outline: none;
    cursor: pointer;
    padding: 14px 16px;
    transition: 0.3s;
    font-size: 17px;
}

/* Change background color of buttons on hover */

div.tab button:hover {
    background-color: #ddd;
}

/* Create an active/current tablink class */

div.tab button.active {
    background-color: #ccc;
}

/* Style the tab content */

.tabcontent {
    padding: 0px 12px;
    /*border: 1px solid #ccc;*/
    border-top: none;
}

.tabcontent input {
    width: 100px;
    height: 25px;
}

.tabcontent .date {
    width: 165px;
}

@media (min-width: 600px) {
    .tabcontent {
        font-size: x-small;
    }
}

@media (min-width: 1600px) {
    .tabcontent {
        font-size: inherit;
    } 
}
/* Style the close button */

.topright {
    float: right;
    cursor: pointer;
    font-size: 20px;
}

.topright:hover {
    color: red;
}

#para {
    display: inline-block;
}

#csv {
    display: inline-block;
}

#select {
